<template>
	<div class="cart">
		<h4></h4>
		<div class="cart-main">
			<div class="cart-th">
				<div class="cart-th1"></div>
				<div class="cart-th2"></div>
				<div class="cart-th3"></div>
				<div class="cart-th4"></div>
				<div class="cart-th5"></div>
				<div class="cart-th6"></div>
			</div>
			<div class="cart-body">
				<ul class="cart-list" v-for="(cart, index) in cartInfoList" :key="cart.id">
					<li class="cart-list-con1">
						<input type="checkbox" name="chk_list" :checked="cart.isChecked" @change="changeChecked(cart, $event)" />
					</li>
					<li class="cart-list-con2">
            <img :src="cart.imgUrl" />
            <div class="item-msg">
              {{ cart.skuName }}
            </div>
          </li>
					<li class="cart-list-con4">
            <span class="price">{{ cart.skuPrice }}.00</span>
          </li>
					<li class="cart-list-con5">
						<a class="mins" @click="minusSkuNum(cart)">-</a>
						<input 
							type="text"
							class="itxt"
							:value="cart.skuNum"
							min="1"
							@input="changeSkuNum(cart, $event)"
							autocomplete="off"
						/>
						<a class="plus" @click="addSkuNum(cart)">+</a>
					</li>
					<li class="cart-list-con6">
            <span class="sum">{{ cart.skuNum * cart.skuPrice }}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="deleteCartById(cart)"></a>
            <br />
            <a href="#none"></a>
          </li>
				</ul>
			</div>
		</div>
		<div class="cart-tool">
			<div class="select-all">
				<input 
					type="checkbox"
					class="chooseAll"
					:checked="isCartChecked && cartInfoList.length > 0"
					@change="updateAllChecked($event)"
				/>
				<span></span>
			</div>
			<div class="option">
				<a @click="deleteAllCart"></a>
				<a href="#none"></a>
        <a href="#none"></a>
			</div>
			<div class="money-box">
				<div class="chosed">
					 <span>{{ cartInfoList.length }}</span>
				</div>
				<div class="sumprice">
					<em> </em>
					<i class="summoney">{{ totalPrice }}</i>
				</div>
				<div class="sumbtn">
					<!-- :, -->
					<router-link class="sum-btn" to="/trade"></router-link>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';
	//lodash
	import throttle from 'lodash/throttle';
	//lodash
	import debounce from 'lodash/debounce';

	export default {
		name: "ShopCart",
		//,
		mounted() {
			this.getData();
		},
		computed: {
			...mapGetters(["CartInfo"]),
			//
			cartInfoList() {
				return this.CartInfo.cartInfoList || [];
			},
			//
			totalPrice() {
				return this.cartInfoList.reduce((prev, curr) => prev + curr.skuPrice * curr.skuNum, 0);
			},
			isCartChecked() {
				//()
				return this.cartInfoList.filter(item => item.isChecked == "1").length === this.cartInfoList.length;
			},
		},
		methods: {
			//
			getData() {
				this.$store.dispatch("getShopCart");
			},
			//->
			async addSkuNum(cart) {
        //
        let params = { skuId: cart.skuId, skuNum: 1 };
        //:
        //
        try {
          //
          await this.$store.dispatch("addOrUpdateCart", params);
          //
          this.getData();
        } catch (error) {
          alert("");
        }
			},
			//-
			minusSkuNum: throttle(async function(cart) {
        if (cart.skuNum > 1) {
          //:1
          let params = { skuId: cart.skuId, skuNum: -1 };
          //
          try {
            //
            await this.$store.dispatch("addOrUpdateCart", params);
            //
            this.getData();
          } catch (error) {
            alert("");
          }
        }
			}, 2000),
			//SkuNum
			changeSkuNum: debounce(async function(cart, e) {
        //
        let params = { skuId: cart.skuId};
        //SkuNum
        let userResultValue = e.target.value * 1;
        //
        if (isNaN(userResultValue) || userResultValue < 1) {
          params.skuNum = 0;
        } else {
          //
          params.skuNum = parseInt(userResultValue) - cart.skuNum;
        }
        //
        try {
          //
          await this.$store.dispatch("addOrUpdateCart", params);
          this.getData();
        } catch (error) {
          alert("");
        }
			}, 500),
			//
			async deleteCartById(cart) {
        //
        let skuId = cart.skuId;
        try {
          // 
          await this.$store.dispatch("deleteCartById", skuId);
          //
          this.getData();
        } catch (error) {
          alert("");
        }
			},
			//
      async changeChecked(cart, e) {
        //
        let params = {
          skuId: cart.skuId,
          isChecked: e.target.checked ? "1" : "0",
        };
        try {
          //:
          await this.$store.dispatch("changeChecked", params);
          //
          this.getData();
        } catch (error) {
          alert("");
        }
      },
      //
      async updateAllChecked(e) {
        //,1|0
        let isChecked = e.target.checked ? "1" : "0";
        try {
          //await:
          await this.$store.dispatch("allUpdateChecked", isChecked);
          //
          this.getData();
        } catch (error) {
          alert("");
        }
      },
      //
      async deleteAllCart() {
        try {
          //
          await this.$store.dispatch("deleteAllCart");
          //
          this.getData();
        } catch (error) {
          alert("");
        }
      },
		},
	}
</script>

<style scoped lang="less">
	.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con3 {
          width: 20.8333%;

          .item-txt {
            text-align: center;
          }
        }

        .cart-list-con4 {
          width: 10%;
        }

        .cart-list-con5 {
          width: 17%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>