//detail
//:statemutationsactionsgetters 
//
import { reqDetailList, reqAddOrUpdateCart } from "@/api";

//
let state = {
  //
  detailInfo: {},
}

//
let mutations = {
  GETDETAILINFO(state, detailInfo) {
    state.detailInfo = detailInfo;
  },
}

// if
let actions = {
  //actions
  //actions:
  async getDetailInfo({ commit, state, dispatch }, skuId) {
    //ID
    let result = await reqDetailList(skuId);
    //200->
    if (result.code === 200) {
      //mutation
      commit("GETDETAILINFO", result.data);
    }
  },
  //|,K,V
  async addOrUpdateCart({ state, commit, dispatch }, { skuId, skuNum }) {
    //(),
    let result = await reqAddOrUpdateCart(skuId, skuNum);
    // console.log(result);
    if (result.code === 200) {
      //,promise
      return "ok";
    } else {
      //Promise
      return Promise.reject();
    }
  },
}

//
let getters = {
  //
  categoryView() {
    //:
      //:state.detailInfo,.categoryView->undefined
      //state.detailInfo,,{7K}
      //:undefined
    return state.detailInfo.categoryView || {};
  },
  //
  skuInfo() {
    return state.detailInfo.skuInfo || {};
  },
  //
  spuSaleAttrList() {
    return state.detailInfo.spuSaleAttrList || [];
  },
};

//
export default {
  state,
  mutations,
  actions,
  getters
}