<template>
  <div class="floor">
    <div class="py-container">
      <div class="title clearfix">
        <h3 class="fl">{{ floor.name }}</h3>
        <div class="fr">
          <ul class="nav-tabs clearfix">
            <li v-for="(nav, index) in floor.navList" :key="index">
              <a href="#tab1" data-toggle="tab">{{ nav.text }}</a>
            </li>
          </ul>
        </div>
      </div>
      <div class="tab-content">
        <div class="tab-pane">
          <div class="floor-1">
            <div class="blockgary">
              <ul class="jd-list">
                <li v-for="(keyword, index) in floor.keywords" :key="index">{{ keyword }}</li>
              </ul>
              <img :src="floor.imgUrl" />
            </div>
            <div class="floorBanner">
              <div class="swiper-container" ref="floor1Swiper">
                <div class="swiper-wrapper">
                  <div class="swiper-slide" v-for="(item, index) in floor.carouselList" :key="item.id" ref="box">
                    <img :src="item.imgUrl" />
                  </div>
                </div>
                <!--  -->
                <div class="swiper-pagination"></div>

                <!--  -->
                <div class="swiper-button-prev"></div>
                <div class="swiper-button-next"></div>
              </div>
            </div>
            <div class="split">
              <span class="floor-x-line"></span>
              <div class="floor-conver-pit">
                <img :src="floor.recommendList[0]" />
              </div>
              <div class="floor-conver-pit">
                <img :src="floor.recommendList[1]" />
              </div>
            </div>
            <div class="split center">
              <img :src="floor.bigImg" />
            </div>
            <div class="split">
              <span class="floor-x-line"></span>
              <div class="floor-conver-pit">
                <img :src="floor.recommendList[2]" />
              </div>
              <div class="floor-conver-pit">
                <img :src="floor.recommendList[3]" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//swiper:
//:
import Swiper from "swiper";
import "swiper/css/swiper.min.css";

export default {
  name:"Floor",
  props: ["floor"],
  mounted() {
    // console.log(this.floor);
    // :Floormounted!!!
    // floorprops,. ->->->mounted
    // Swiper
    /*
    var mySwiper = new Swiper(this.$refs.floor1Swiper, {
       // 
        direction: "horizontal",
        // 
        loop: true,
        // 
        pagination: {
          el: ".swiper-pagination",
          // 
          type: "bullets",
          // 
          clickable: true,
        },
        // 
        autoplay: {
          delay: 2000,
          //5
          // pauseOnMouseEnter: true,
          // trueslide
          stopOnLastSlide: true,
          // swiperautoplay
          disableOnInteraction: false,
        },
        // 
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        // 
        effect: "cube",
    });

    //1:swiper,Swiper
    //2. Swiper 1CSS|DOM  2
    //
    mySwiper.el.onmouseover = function() {
      mySwiper.autoplay.stop();
    };
    // 
    mySwiper.el.onmouseout = function() {
      mySwiper.autoplay.start();
    };
    */
  },
  watch: {
    //floorprops:props
    floor: {
      immediate: true,
      handler() {
        this.$nextTick(() => {
          //Swiper
          var mySwiper = new Swiper(this.$refs.floor1Swiper, {
            direction: 'horizontal',  // 
            loop: true,  // 
            pagination: {
              // 
              el: ".swiper-pagination",
              type: "bullets",  // 
              clickable: true,  // 
            },
            autoplay: { 
              //
              delay: 2000,
              stopOnLastSlide: true,  // trueslide
              disableOnInteraction: false,   //swiperautoplay
            },
            navigation: {
              // 
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev",
            },
            effect: "cube",  // 
          });
          //1:swiper,Swiper
          //2:Swiper 1CSS|DOM  2
          //
          mySwiper.el.onmouseover = function() {
            mySwiper.autoplay.stop();
          };
          mySwiper.el.onmouseout = function() {
            mySwiper.autoplay.start();
          };
        });
      },
    },
  },
}
</script>

<style scoped lang="less">
.floor {
  margin-top: 15px;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .title {
      .fl {
        float: left;
        color: #c81623;
        font-size: 20px;
        line-height: 30px;
        margin: 9px 0;
        font-weight: 700;
      }

      .fr {
        float: right;

        .nav-tabs {
          margin: 10px 0 0;
          display: inline-block;

          li {
            float: left;
            line-height: 18px;

            a {
              padding-top: 1px;
              font-weight: 400;
              background-color: #fff;

              &::after {
                content: "|";
                padding: 0 10px;
              }
            }

            &:nth-child(7) {
              a {
                &::after {
                  content: "";
                }
              }
            }

            &.active {
              a {
                color: #e1251b;
              }
            }
          }
        }
      }
    }

    .tab-content {
      border-top: 2px solid #c81623;
      border-bottom: 1px solid #e4e4e4;

      .tab-pane {
        .floor-1 {
          height: 360px;
          display: flex;

          .blockgary {
            width: 210px;
            height: 100%;
            background-color: #f7f7f7;

            .jd-list {
              padding: 15px 0;
              overflow: hidden;

              li {
                list-style-type: none;
                float: left;
                width: 40%;
                margin: 0 10px;
                border-bottom: 1px solid #e4e4e4;
                text-align: center;
                line-height: 26px;
              }
            }

            img {
              width: 100%;
            }
          }

          .floorBanner {
            width: 330px;
            height: 100%;
          }

          .split {
            width: 220px;
            height: 100%;
            position: relative;

            .floor-x-line {
              position: absolute;
              background-color: #e4e4e4;
              width: 220px;
              height: 1px;
              top: 180px;
            }

            .floor-conver-pit {
              width: 100%;
              height: 50%;

              img {
                width: 100%;
                height: 50%;
                transition: all 400ms;

                &:hover {
                  opacity: 0.8;
                }
              }
            }
          }

          .center {
            border: 1px solid #e4e4e4;
          }
        }
      }
    }
  }
}
</style>