<script>
	import ImageList from "./ImageList";
	import Zoom from "./Zoom";
	import { mapGetters } from "vuex";

	export default {
		name: "Detail",
		components: { ImageList, Zoom },
		data() {
			return {
				//
				skuNum: 1,
			};
		},
		mounted() {
			//action:id
			this.$store.dispatch("getDetailInfo", this.$route.params.skuId);
			// console.log("mounted,");
		},
		computed: {
			...mapGetters(["categoryView", "skuInfo", "spuSaleAttrList"]),
		},
		methods: {
			changeChecked(saleAttrValue, arr) {
				//:
				//:
				//    !!!!
				//
				//:,
				arr.forEach((item) => {
					item.isChecked = "0";
				});
				saleAttrValue.isChecked = "1";
			},
			//change
			handler(e) {
				//event[]
				let value = e.target.value * 1;
				//
				if (isNaN(value) || value < 1) {
					this.skuNum = 1;
				} else {
					// 
					this.skuNum = parseInt(value);
				}
			},
			//
			async addOrUpdateCart() {
				//action:id
				//?
				//->addOrUpdateCart,asyc,Promise
				//Promise:pendingaddOrUpdateCart
				try {
					await this.$store.dispatch("addOrUpdateCart", {
						skuId: this.$route.params.skuId,
						skuNum: this.skuNum,
					});
					//:,
					//
					sessionStorage.setItem("SKUINFO", JSON.stringify(this.skuInfo));
					// 
					this.$router.push({
						path: "/addcartsuccess",
						query: { skuNum: this.skuNum},
					});
				} catch (error) {
					// 
					alert("");
				}
			},
		},
	}
</script>

