import VueRouter from "vue-router";
import Vue  from "vue";

Vue.use(VueRouter);

//
import routes from './routes';

//
import store from '@/store'

//push
let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;

//VueRouter.prototype
//location:
VueRouter.prototype.push = function(location, resolve, reject) {
    //thisVueRouter
    //pushthiswindows
    //pushpushthis,vueRouter

    //:applycall?
    //:this
    //:apply   call 

    if (resolve && reject) {
      //:
      originPush.call(this, location, resolve, reject);
    } else {
      originPush.call(this, location, () => {}, () => {});
    }
}

VueRouter.prototype.replace = function(location, resolve, reject) {
  if (resolve && reject) {
    //:
    originReplace.call(this, location, resolve, reject);
  } else {
    originReplace.call(this, location, () => {}, () => {});
  }
}


// :VueRouter
// ,VueRouter,
const router = new VueRouter({
  // 
  routes,
  // 
  scrollBehavior() {
    //,,
    //x|y [x|y]
    return { y: 0 };
  }
});

export default router;


//:

//:

//
router.beforeEach(async (to, from, next) => {
  //to:
  //from:
  //next:!!!!!! 
  //next(),
  //:next(path),?

  //:token
  //,
  //token
  let hasToken = store.state.user.token;
  // 
  let hasNickName = store.state.user.nickName;
  let hasPhone = store.state.user.phone;
  // 
  if (hasToken) {
    //,login
    if (to.path === "/login") {
      next("/home");
    } else {
      //,login
      if (hasNickName || hasPhone) {
        next();
      } else {
        //, 
        try {
          //
          await store.dispatch("getUserInfo");
          next();
        } catch(error) {
          //token
          //token
          //token:token
          await store.dispatch("logout");
          //
          next("/login");
        }
      }
    }
  } else {
    //||.''
    //:/trade/pay/paysuccess/center/center/myorder  /center/teamorder
    let toPath = to.path;
    const notAllowedPath = ["/trade", "/pay", "/paysuccess", "/center", "/center/myorder", "/center/teamorder"];
    // if (toPath.indexOf('trade') != -1 || toPath.indexOf('pay') != -1 || toPath.indexOf('center') != -1) {
    //   next("/login?redirect="+toPath);
    // }else {
    //   next();
    // }
    if (notAllowedPath.includes(toPath)) {
      next("/login?redirect="+toPath);
    } else {
      next();
    }
  }
});

