//

import {reqGetCode, reqRegister, reqUserLogin, reqUserInfo, reqUserLogout } from "@/api";

let state = {
  code: "",  // 
  token: localStorage.getItem("TOKEN"),   // vuex
  nickName: "",  // 
  phone: "", // 
};

let mutations = {
  GETCODE(state, code) {
    state.code = code;
  },
  SET_TOKEN(state, token) {
    state.token = token;
  },
  SET_PHONE(state, phone) {
    state.phone = phone;
  },
  SET_USERINFO(state, nickName) {
    state.nickName = nickName;
  },
  CLEAR(state) {
    //
    state.token = "";
    state.nickName = "";
    state.code = "";
    state.phone = "";
    localStorage.removeItem("TOKEN");  //
  },
};

let actions = {
  // 
  async getCode({ commit, state, dispatch }, phone) {
    let result = await reqGetCode(phone);
    if (result.code === 200) {
      commit("GETCODE", result.data);
      return "ok";
    } else {
      return Promise.reject();
    }
  },
  // 
  async registerUser({ commit, state, dispatch }, userInfo) {
    //data,mutation
    let result = await reqRegister(userInfo);
    if (result.code === 200) {
      // 
      return "ok";
    } else {
      // 
      return Promise.reject(new Error(result.message));
    }
  },
  //:
  async userLogin({ commit, state, dispatch }, userInfo) {
    //:100%!!!
      /*
        
          {
                code:200,
                data:{
                    token:'1e4vdadhajkhdakj6sahdajk'
                },
                message:''
          }
      */
    let result = await reqUserLogin(userInfo);
    //
    if (result.code === 200) {
      commit("SET_TOKEN", result.data.token);
      commit("SET_PHONE", userInfo.phone);
      //:token
      localStorage.setItem("TOKEN", result.data.token);
      return "ok";
    } else {
      // 
      return Promise.reject(new Error(result.message));
    }
  },
  //
  async getUserInfo({ commit, state, dispatch }) {
    let result = await reqUserInfo();
    if (result.code === 200) {
      commit("SET_USERINFO", result.data.nickName);
      return "ok";
    } else {
      return Promise.reject();
    }
  },
  // 
  async logout({ commit, state, dispatch }) {
    //token
    let result = await reqUserLogout();
    if (result.code === 200) {
      commit("CLEAR");
      return "ok";
    } else {
      return Promise.reject(new Error(result.message));
    }
  },
};

let getters = {
  userName() {
    return state.nickName || state.phone;
  },
};

export default {
  state,
  mutations,
  actions,
  getters
}
