<template>
  <div class="type-nav">
    <div class="container">
      <!--  -->
      <div @mouseleave="leaveHandler" @click="goSearch">
        <h2 class="all" @mouseenter="changeShow"></h2>
        <!--  -->
        <transition name="sort">
          <div class="sort" v-show="show">
            <div class="all-sort-list2">
              <!--  -->
              <div class="item" v-for="(c1, index) in category" :key="c1.categoryID">
                <h3 @mouseenter="enterHandler(index)" :class="{ active: currentIndex === index}">
                  <a :data-categoryName="c1.categoryName" :data-category1Id="c1.categoryId">{{ c1.categoryName }}</a>
                </h3>
                <!-- JS(display:none|block) -->
                <div class="item-list clearfix" :style="{ display: currentIndex === index ? 'block' : 'none'}">
                  <!--  -->
                  <div class="subitem" v-for="(c2, index) in c1.categoryChild" :key="c2.categoryID">
                    <dl class="fore">
                      <dt>
                        <a :data-categoryName="c2.categoryName" :data-category2Id="c2.categoryId">{{ c2.categoryName }}</a>
                      </dt>
                      <dd>
                        <!--  -->
                        <em v-for="(c3, index) in c2.categoryChild">
                          <a :data-categoryName="c3.categoryName" :data-category3Id="c3.categoryId">{{ c3.categoryName }}</a>
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
      </nav>
    </div>
  </div>
</template>

<script>
  //state--->mapState
  //mapState:
  import { mapState } from 'vuex';

  //:lodashAPI,
  // import _ from "lodash";
  //:
  import throttle from "lodash/throttle"

  export default {
    name: "TypeNav",
    data() {
      return {
        //,
        currentIndex: -1,
        show: true,  // 
      };
    },
    methods: {
      //
      enterHandler: throttle(function(index) {
        //
        this.currentIndex = index;
        //,,
        //,,!!!!
        //
        // console.log("" + index);
      }, 10),
      //
      leaveHandler() {
        //
        this.currentIndex = -1;
        //
        //:search,
        if (this.$route.path !== "/home") {
          this.show = false;
        }
      },
      //
      changeShow() {
        //:search,
        if (this.$route.path !== "/home") {
          this.show = true;
        }
      },
      //
      //->
      goSearch(event) {
        //:divh3h2emdtdddl...aa
        //:acategory1Id|category2Id|category2Id
        let targetNode = event.target;
        //a:data-categoryName
        let { categoryname, category1id, category2id, category3id } = targetNode.dataset;
        //a1|2|3
        if (categoryname) {
          //a,search
          let locations = {
            name: 'search',
            query: { categoryName: categoryname },
          };
          //a
          if (category1id) {
            locations.query.category1Id = category1id;
          } else if (category2id) {
            locations.query.category2Id = category2id;
          } else if (category3id) {
            locations.query.category3Id = category3id;
          }
          //,params,search
          if (this.$route.params.keyword) {
            locations.params = this.$route.params;
          }
          //query
          this.$router.push(locations);
        }
      },
    },

    //Home|SearchTypeNav,TypeNavHomeSearch
    //VC$route,TypeNav
    mounted() {
      //action
      //,
      if (this.$route.path !== "/home") {
        this.show = false;
      }
    },
    // 
    computed: {
      //:stateK  ...mapState(['home'])
      ...mapState({
        //,K,VC
        //erha,
        //state->
        category: (state) => state.home.category,  //
      }),
    },
  };
</script>

<style scoped lang="less">
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;
    
    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background-color: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }

            &.active {
              background-color: #FFE7BF;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            left: 210px;
            top: 0;
            width: 734px;
            min-height: 460px;
            background-color: #f7f7f7;
            border: 1px solid;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;

                  a:hover {
                    background-color: skyblue;
                  }
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;

                    a:hover {
                      background-color: skyblue;
                    }
                  }
                }
              }
            }
          }

          //  &:hover {
          //   .item-list {
          //     display: block;
          //   }
          // }
          
        }
      }
    }

    /*: */
    .sort-enter {
      height: 0px;
    }

    .sort-enter-active {
      transition: all 0.3s;
    }
    .sort-enter-to {
      height: 461px;
    }
  }
}
</style>