//home
//:statemutationsactionsgetters 
//
import { reqBaseCategoryList, reqBannerList, reqFloorList } from "@/api";

//
let state = {
  //,
  category: [],  //
  bannerList:[],  //
  floorList: [],  //
}

//
let mutations = {
  GETCATEGORY(state, category) {
    state.category = category;
  },
  GETBANNERLIST(state, bannerList) {
    state.bannerList = bannerList;
  },
  GETFLOORLIST(state, floorList) {
    state.floorList = floorList;
  }
}

// if
let actions = {
  //actions
  //actions:
  async getCategory({ commit, state, dispatch }) {
    //,vuex
    //reqCategory,Promisepending
    //await 
    let result = await reqBaseCategoryList();
    //200->
    if (result.code === 200) {
      //mutation
      commit("GETCATEGORY", result.data);
    }
  },
  //banneraction
  async getBannerList({commit, state, dispatch}) {
    //
    let result = await reqBannerList();
    if (result.code === 200) {
      commit("GETBANNERLIST", result.data);
    }
  },
  //Floor
  async getFloorList({commit, state, dispatch}) {
    //
    let result = await reqFloorList();
    if (result.code === 200) {
      commit("GETFLOORLIST", result.data);
    }
  },

}

//
let getters = {};

//
export default {
  state,
  mutations,
  actions,
  getters
}