<template>
	<div class="trade-container">
		<h3 class="title"></h3>
		<div class="content">
			<!--  -->
			<h5 class="receive"></h5>
			<div class="address clearFix" v-for="(user, index) in address" :key="user.id">
				<span class="username" :class="{ selected: user.isDefault == '1' }">{{ user.consignee }}</span>
				<p $click="changeDefault(user)">
					<span class="s1">{{ user.fullAddress }}</span>
					<span class="s2">{{ user.phoneNum }}</span>
					<span class="s3" v-show="user.isDefault == '1'"></span>
				</p>
			</div>
			<div class="line"></div>
			<h5 class="pay"></h5>
			<div class="address clearFix">
				<span class="username selected"></span>
				<span class="username" style="margin-left: 5px;"></span>
			</div>
			<div class="line"></div>
			<h5 class="pay"></h5>
			<div class="way">
				<h5></h5>
				<div class="info clearFix">
					<span class="s1"></span>
					<p>81009:00-15:00</p>
				</div>
			</div>
			<!--  -->
			<div class="detail">
				<h5></h5>
				<ul class="list clearFix" v-for="(shop, index) in tradeInfo.detailArrayList" :key="shop.skuId">
					<li>
						<img :src="shop.imgUrl" alt="" style="width: 100px;" />
					</li>
					<li>
						<p>{{ shop.skuName }}</p>
						<h4>7</h4>
					</li>
					<li>
            <h3>{{ shop.orderPrice }}.00</h3>
          </li>
          <li>X{{ shop.skuNum }}</li>
          <li></li>
				</ul>
			</div>
			<div class="bbs">
        <h5></h5>
        <textarea
          placeholder=""
          class="remarks-cont"
          v-model="msg"
        ></textarea>
      </div>
			<div class="line"></div>
      <div class="bill">
        <h5></h5>
        <div>  </div>
        <h5>/</h5>
      </div>
		</div>
		<div class="money clearFix">
			<ul>
				<li>
					<b><i>{{ tradeInfo.totalNum }}</i></b>
					<span>{{ tradeInfo.totalAmount }}.00</span>
				</li>
				<li>
					<b></b>
					<span>0.00</span>
				</li>
				<li>
          <b></b>
          <span>0.00</span>
        </li>
			</ul>
		</div>
		<div class="trade">
			<div class="price">
				:<span>{{ tradeInfo.totalAmount }}.00</span>
			</div>
			<div class="receiveInfo">
        :
        <span>{{ defaultUser.fullAddress }}</span>
        <span>{{ defaultUser.consignee }}</span>
        <span>{{ defaultUser.phoneNum }}</span>
      </div>
		</div>
		<div class="sub clearFix">
			<!-- ? -->
			<a class="subBtn" @click="submitInfo"></a>
		</div>
	</div>
</template>

<script>
	import { mapState } from 'vuex';

	export default {
		 //,
		name:"Trade",
		data() {
			return {
				msg: "",
			}
		},
		mounted() {
			this.getAddress();
			this.getShopInfo();
		},
		computed: {
			...mapState({
				address: (state) => state.trade.address,
				tradeInfo: (state) => state.trade.tradeInfo,
				orderId: (state) => state.trade.payId,
			}),
			//
			defaultUser() {
				//find:,.|
				return this.address.find((item) => item.isDefault == "1") || {};
			}
		},
		methods: {
			//
			getAddress() {
				this.$store.dispatch("getAddress");
			},
			// 
			getShopInfo() {
				this.$store.dispatch("getShopInfo");
			},
			//
			changeDefault(user) {
				//isDefault = 0
				this.address.forEach((item) => {
					item.isDefault = "0";
				});
				user.isDefault = "1";
			},
			//
			async submitInfo() {
				// 
				let tradeNo = this.tradeInfo.tradeNo;
				let data = {
					consignee: this.defaultUser.consignee,  // 
					consigneeTel: this.defaultUser.phoneNum,  // 
					deliveryAddress: this.defaultUser.fullAddress,  // 
					paymentWay: "ONLINE",  // 
					orderComment: this.msg,  // 
					orderDetailList: this.tradeInfo.detailArrayList,  // 
				};
				//:
				try {
					await this.$store.dispatch("submitInfo", { tradeNo, data});
					//IDpay
					this.$router.push({ path: "/pay", query: {orderId: this.orderId } });
				} catch (error) {
					alert(error.message);
				}
			},
		},
	}
</script>

<style scoped lang="less">
.trade-container {
	.title {
		width: 1200px;
		margin: 0 auto;
		font-size: 14px;
		line-height: 21px;
	}

	.content {
		width: 1200px;
		margin: 10px auto 0;
		border: 1px solid rgb(221, 221, 221);
		padding: 25px;
		box-sizing: border-box;

		.receive, .pay {
			line-height: 36px;
			margin: 18px 0;
		}

		.address {
			padding-left: 20px;
			margin-bottom: 15px;

			.username {
				float: left;
				width: 100px;
				height: 30px;
				line-height: 30px;
				text-align: center;
				border: 1px solid #ddd;
				position: relative;
			}

			.username::after {
				content: "";
				display: none;
				width: 13px;
				height: 13px;
				position: absolute;
				bottom: 0;
				right: 0;
				background: utl("./images/choosed.png") no-repeat;
			}

			.username.selected {
				border-color: #e1251b;
			}

			.username.selected::after {
				display: block;
			}

			p {
				width: 610px;
				float: left;
				line-height: 30px;
				margin-left: 10px;
				padding-left: 5px;
				cursor: pointer;

				.s1 {
					float: left;
				}

				.s2 {
					float: left;
					margin: 0 5px;
				}

				.s3 {
					float: left;
					width: 56px;
					height: 24px;
					line-height: 24px;
					margin-left: 10px;
					background-color: #878787;
					color: #fff;
					margin-top: 3px;
					text-align: center;
				}
			}

			p:hover {
				background-color: #ddd;
			}
		}

		.line {
			height: 1px;
			background-color: #ddd;
		}

		.way {
			width: 1080px;
			height: 110px;
			background-color: #F4F4F4;
			padding: 15PX;
			margin: 0 auto;

			h5 {
				line-height: 50px;
			}

			.info {
				margin-top: 20px;

				.s1 {
          float: left;
          border: 1px solid #ddd;
          width: 120px;
          height: 30px;
          line-height: 30px;
          text-align: center;
          margin-right: 10px;
        }

        p {
          line-height: 30px;
        }
			}
		}

		.detail {
			width: 1080px;
			background: #feedef;
			padding: 15px;
			margin: 2px auto 0;

			h5 {
				line-height: 50px;
			}

			.list {
				display: flex;
				justify-content: space-between;

				li {
					line-height: 30px;

					p {
						margin-bottom: 20px;
					}

					h4 {
						color: #c81623;
						font-weight: 400;
					}

					h3 {
						color: #e12228;
					}
				}
			}
		}
		.bbs {
			margin-bottom: 15px;

			h5 {
				line-height: 50px;
			}

			textarea {
				width: 100%;
				border-color: #e4e2e2;;
				line-height: 1.8;
				outline: none;
				resize: none;
			}
		}

		.bill {
			h5 {
				line-height: 50px;
			}

			div {
				padding-left: 15px;
			}
		}
	}

	.money {
		width: 1200px;
		margin: 20px auto;

		ul {
			width: 220px;
			float: right;

			li {
				line-height: 30px;
				display: flex;
				justify-content: space-between;

				i {
					color: red;
				}
			}
		}
	}

	.trade {
		box-sizing: border-box;
		width: 1200px;
		padding: 10px;
		margin: 10px auto;
		text-align: right;
		background-color: #f4f4f4;
		border: 1px solid #ddd;

		div {
			line-height: 30px;
		}

		.price span {
			color: #e12228;
			font-weight: 700;
			font-size: 14px;
		}

		.receiveInfo {
			color: #999;
		}
	}
	
	.sub {
		width: 1200px;
		margin: 0 auto 10px;

		.subBtn {
			float: right;
			width: 164px;
			height: 56px;
			font: 700 19px "";
			line-height: 56px;
			text-align: center;
			color: #fff;
			background-color: #e1251b;
		}
	}
}
</style>