<template>
<div class="register-container">
		<!--  -->
		<div class="register">
			<h3><span class="go"> <router-link to="/login"></router-link> </span></h3>

			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model="phone" />
				<span class="error-msg"></span>
			</div>
			
			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model="code" />
				<button style="height: 38px;" @click="getCode"></button>
				<span class="error-msg"></span>
			</div>

			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model="password" />
				<span class="error-msg"></span>
			</div>

			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model="password1" />
				<span class="error-msg"></span>
			</div>

			<!--  -->
			<div class="controls">
				<input type="checkbox" name="m1" :checked="agree" />
				<span>xx</span>
				<span class="error-msg"></span>
			</div>

			<!--  -->
			<div class="btn">
				<button @click="register"></button>
			</div>
		</div>

		<!--  -->
		<div class="copyright">
			<ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address">XXXXXXXXX</div>
      <div class="beian">ICP6666666</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Register",
		data() {
			return {
				phone: "",  // 
				code: "",  // 
				password: "",  // 
				password1: "",  // 
				agree: true,  // 
			}
		},
		methods: {
			//
			async getCode() {
				const { phone } = this;
				//
				if (phone) {
					try {
						//
						await this.$store.dispatch("getCode", phone);
						//VCcode,
						this.code = this.$store.state.user.code;
					} catch(error) {}
				}
			},
			//
			async register() {
				// 
				const { phone, code, password, password1 } = this;
				//
				if (phone && code && password == password1) {
					//vuex
					try {
						//
						await this.$store.dispatch("registerUser", { phone, code, password });
						//
						this.$router.push("/login");
					} catch (error) {
						// 
						alert(error.message);
					}
				}
			},
		},
	}
</script>

<style scoped lang="less">
