<template>
	<header class="header">
		<!--  -->
		<div class="top">
			<div class="container">
				<div class="loginList">
					<p></p>
					<!--  -->
					<p>
						<span></span>
						<router-link to="/login" class="register"></router-link>
						<router-link to="/register" class="register"></router-link>
						<!--  -->
					</p>
					<!--  -->
					<p>
						<a>{{ userName || "" }}</a>
						<a class="register" @click="logout"></a>
					</p>
				</div>
				<div class="typeList">
					<router-link to="/center"></router-link>
					<router-link to="/shopcart"></router-link>
					<a href="#"></a>
					<a href="#"></a>
					<a href="#"></a>
					<a href="#"></a>
					<a href="#"></a>
					<a href="#"></a>
				</div>
			</div>
		</div>
		<!-- ,  -->
		<div class="bottom">
			<!-- logo -->
			<h1 class="logoArea">
				<router-link to="/home" class="logo">
					<img src="./images/logo.png" alt="logo" />
				</router-link>
			</h1>
			<!--  -->
			<div class="searchArea">
				<form action="#" class="searchForm">
					<input type="text" id="autocomplete" class="input-error input-xxlarge" v-model="keyword">
					<!--  -->
					<button class="sui-btn btn-xlarge btn-danger" @click="goSearch"></button>
				</form>
			</div>
		</div>
	</header>
</template>

<script>
	import { mapGetters } from 'vuex';

	export default {
		//,
		name: "Header",
		data() {
			return {
				// 
				keyword: "",
				
			}
		},
		computed: {
			...mapGetters(["userName"]),
		},
		methods: {
			// 
			goSearch() {
				//,.
				// 

				// query
				//this.$router.push({name: "search", query:{keyword:this.keyword}});
				
				//params [,]
      	// this.$router.push({name:'search',params:{keyword:this.keyword}})
				
				// query+params
				//this.$router.push({name: "search", params:{keyword: "abc" || undefined}, query:{keyword:this.keyword}});

				// Vue-RouterPromise,,push
				// 
				// let reuslt = this.$router.push({name: "search", params:{keyword: "abc" || undefined}, query:{keyword:this.keyword}}, (data)=>{console.log(data)}, (err)=>{console.error(err)});
				// console.log(reuslt);

				// ,query,search
				let locations = {
					name: "search",
					params: {keyword: this.keyword || undefined}
				};
				//query
				if (this.$route.query.categoryName) {
					locations.query = this.$route.query;
				}
				this.$router.push(locations);
			},
			// 
			logout() {
				this.$store.dispatch("logout");
			}
		},
		mounted() {
			//
			this.$bus.$on("clearKeyword", () => {
				// console.log("clearKeyword");
				this.keyword = "";
			});
		},
	}
</script>

<style scoped lang="less">
.header {
	& > .top {
		background-color: #eaeaea;
		height: 30px;
		line-height: 30px;

		.container {
			width: 1200px;
			margin: 0 auto;
			overflow: hidden;

			.loginList {
				float: left;

				p {
					float: left;
					margin-right: 10px;

					.register {
						border-left: 1px solid #b3aeae;
						padding: 0 5px;
						margin-left: 5px;
					}
				}
			}

			.typeList {
				float:right;

				a {
					padding: 0 10px;

					& + a {
						border-left: 1px solid #b3aeae;
					}
				}
			}
		}
	}

	& > .bottom {
		width: 1200px;
		margin: 0 auto;
		overflow:hidden;

		.logoArea {
			float: left;

			.logo {
				img {
					width: 175px;
					margin: 25px 45px;
				}
			}
		}

		.searchArea {
			float: right;
			margin-top: 35px;

			.searchForm {
				overflow: hidden;

				input {
					box-sizing: border-box;
					width: 490px;
					height: 32px;
					padding: 0px 4px;
					border: 2px solid #ea4a36;
					float: left;

					&:focus {
						outline: none;
					}
				}

				button {
					height: 32px;
					width: 68px;
					background-color: #ea4a36;
					border: none;
					color: #fff;
					float: left;
					cursor: pointer;

					&:focus {
						outline: none;
					}
				}
			}
		}
	}
}

</style>